					_id:item._id,
					title:item.title,
					create_time:item.create_time,
					thumbs_up_count:item.thumbs_up_count,
					browse_count:item.browse_count,
					author:item.author
				}
				console.log('打开详情页',params)
				//url传参
				uni.navigateTo({
					url:'/pages/home-detail/home-detail?params='+JSON.stringify(params)
				})
			}
		}
	}
